.instructions-list {
  border-radius: 8px;
  border: 5px solid rgba(255, 255, 255, 0.2);
  padding: 10px;
  margin: 0 !important;
}
.instruction {
  margin: 10px;
  letter-spacing: 0.5px;
  color: rgb(15, 15, 15);
  font-weight: 400;
}

.instructions-container {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
}

.pose-demo-img {
  height: 400px;
  aspect-ratio: 1;
  border-radius: 15px;
}
